export default "import EmblaCarousel from 'embla-carousel';\nimport { addPrevNextButtonClickHandlers } from '../../EmblaCarouselArrowButtons';\nimport { updateSelectedSnapDisplay } from '../../EmblaCarouselSelectedSnapDisplay';\nimport { addSlideSizeFormHandler } from './EmblaCarouselSlideSizeForm';\nimport { addGapSizeFormHandler } from './EmblaCarouselGapSizeForm';\nimport '../css/base.css';\nimport '../css/sandbox.css';\nimport '../css/embla.css';\n\nconst OPTIONS = {};\n\nconst emblaNode = document.querySelector('.embla');\nconst viewportNode = emblaNode.querySelector('.embla__viewport');\nconst prevBtn = emblaNode.querySelector('.embla__button--prev');\nconst nextBtn = emblaNode.querySelector('.embla__button--next');\nconst snapDisplayNode = (emblaNode.querySelector('.embla__selected-snap-display'));\n\nconst slideSizeFormNode = (emblaNode.querySelector('.embla__text-form--slide-size'));\nconst slideSizeInputNode = (slideSizeFormNode.querySelector('input[type=\"text\"]'));\n\nconst slideGapSizeFormNode = (emblaNode.querySelector('.embla__text-form--slide-gap'));\nconst slideGapSizeInputNode = (slideGapSizeFormNode.querySelector('input[type=\"number\"]'));\n\nconst emblaApi = EmblaCarousel(viewportNode, OPTIONS);\n\naddPrevNextButtonClickHandlers(emblaApi, prevBtn, nextBtn);\nupdateSelectedSnapDisplay(emblaApi, snapDisplayNode);\naddSlideSizeFormHandler(emblaApi, slideSizeFormNode, slideSizeInputNode, '100%', '--slide-size');\naddGapSizeFormHandler(emblaApi, slideGapSizeFormNode, slideGapSizeInputNode, 0, 50, 0, '--slide-gap', 'px');\n";